/* Arrows */
.slick-prev,
.slick-next
{
  /*z-index: 9999;*/
  font-size: 0;
  line-height: 0;
  /**/
  position: absolute;
  top: 50%;

  -webkit-transform: translate(0%,-50%);
  -moz-transform: translate(0%,-50%);
  -ms-transform: translate(0%,-50%);
  -o-transform: translate(0%,-50%);
  transform: translate(0%,-50%);

  display: block;

  width: 40px;
  height: 40px;

  cursor: pointer;

  background: none;
  color: blue;
  outline: none;
  border-radius: 50%;
  border : 1px solid #8c8b82;
}
.slick-responsive .slick-next ,
.slick-responsive .slick-prev{
  width: 40px;
  height: 40px;
  background-color: rgba(87, 87, 87, 0.89);
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus
{
  color: #ba3051;
  /*outline: none;*/
  /*background: none;*/
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before
{
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before
{
  opacity: .25;
}

.slick-prev:before,
.slick-next:before
{
  font-family: 'slick';
  font-size: 25px;
  line-height: 1;

  opacity: .75;
  color: #f4749c;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*.slick-prev{  left: 10px; }*/
/*[dir='rtl'] .slick-prev{left:0;}*/

.slick-responsive .slick-prev{ z-index: 9; left: 5px;  }
[dir='rtl'] .slick-responsive .slick-prev{right: 0px}

.slick-prev:before
{
  content: '←';
}
[dir='rtl'] .slick-prev:before
{
  content: '→';
}

/*.slick-next{right: 5px;}*/
/*[dir='rtl'] .slick-next{right: 0;}*/
/**/
.slick-responsive .slick-next{right: 5px;}
[dir='rtl'] .slick-responsive .slick-next{left: 0px;}

.slick-next:before
{
  content: '→';
}
[dir='rtl'] .slick-next:before
{
  content: '←';
}

/* Dots */
.slick-slider
{
  margin-bottom: 30px;
}

.slick-dots
{
  position: absolute;
  bottom: -45px;

  display: block;

  width: 100%;
  padding: 0;

  list-style: none;

  text-align: center;
}
.slick-dots li
{
  position: relative;

  display: inline-block;

  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;

  cursor: pointer;
}
.slick-dots li button
{
  font-size: 0;
  line-height: 0;

  display: block;

  width: 20px;
  height: 20px;
  padding: 5px;

  cursor: pointer;

  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
  outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
  opacity: 1;
}
.slick-dots li button:before
{
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;

  position: absolute;
  top: 0;
  left: 0;

  width: 20px;
  height: 20px;

  content: '•';
  text-align: center;

  opacity: .25;
  color: black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before
{
  opacity: .75;
  color: black;
}


/***************************************/
/* Slider */
.slick-slider
{
  width: 100%;
  margin: auto;
  /*position: relative;*/

  display: block;
  box-sizing: border-box;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list
{
  width: 90%;
  /*position: relative;*/

  display: block;
  overflow: hidden;

  margin: auto;
  padding: 0;
}
.slick-list:focus
{
  outline: none;
}
.slick-list.dragging
{
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track
{
  position: relative;
  top: 0;
  left: 0;

  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
  display: table;

  content: '';
}
.slick-track:after
{
  clear: both;
}
.slick-loading .slick-track
{
  visibility: hidden;
}

.slick-slide
{
  display: none;
  float: left;

  margin: 10px;

  height: 100%;
  min-height: 1px;
}
[dir='rtl'] .slick-slide
{
  float: right;
}
.slick-slide img
{
  display: block;
}
.slick-slide.slick-loading img
{
  display: none;
}
.slick-slide.dragging img
{
  pointer-events: none;
}
.slick-initialized .slick-slide
{
  display: block;
}
.slick-loading .slick-slide
{
  visibility: hidden;
}
.slick-vertical .slick-slide
{
  display: block;

  height: auto;

  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}


.carousel-container {
  z-index: 0; }

.custom-dropdown {
  z-index: 1; }

.carousel-container .carousel-indicators li {
  cursor: pointer; }

.carousel-container .carousel-text {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(-90deg, #171717, transparent); }

.carousel-text button {
  position: absolute;
  left: 75%;
  top: 60%;
  -webkit-transform: translate(-25%, -40%);
  -moz-transform: translate(-25%, -40%);
  -ms-transform: translate(-25%, -40%);
  -o-transform: translate(-25%, -40%);
  transform: translate(-25%, -40%);
}

.carousel-container .carousel-text span {
  font-family: "Calibri Light";
  position: absolute;
  width: 40%;
  top: 0;
  right: 0;
  font-size: 2.5vw;
  font-weight: bold;
}
.carousel-item img {
  height: 400px;
}
@media only screen and (max-width: 768px) {
  main {
     margin-top: 110px;
  }
  .carousel-text span {
    font-size: 1.5rem !important;
  }
  .mainPageSection .btn {
    max-width: 200px;
  }
  .carousel-item img {
    height: 250px;
  }
}

@media only screen and (max-width: 415px) {
  .carousel-item img {
    height: 200px;
  }
  .mainPageSection h2 {
   font-size: 1.8rem;
  }
  .carousel-text span {
    font-size: 1.3rem !important;
  }
}

