#responsiveSearch input {
    direction: rtl;
}


.search-responsive {
    display: flex;
    align-items: flex-start;
    /*width: 700px;*/
}
/*the container must be positioned relative:*/
.autocomplete-responsive {
    width:300px;
    /*width: 31%;*/
    position: relative;
    display: inline-block;
}

.autocomplete-responsive input {
    border: 1px solid transparent;
    background-color: #f1f1f1;
    padding: 5.5px;
    font-size: 16px;
}

.autocomplete-responsive input[type=text] {
    background-color: #f1f1f1;
    width: 100%;
}

.autocomplete-responsive input[type=submit] {
    background-color: DodgerBlue;
    color: #fff;
    cursor: pointer;
}

.autocomplete-items-responsive {
    position: absolute;
    border: 1px solid #d4d4d4;
    border-bottom: none;
    border-top: none;
    z-index: 99;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    right: 0;
}

.autocomplete-items-responsive div {
    padding: 10px;
    cursor: pointer;
    background-color: #fff;
    border-bottom: 1px solid #d4d4d4;
}

/*when hovering an item:*/
.autocomplete-items-responsive div:hover {
    background-color: #e9e9e9;
}

/*when navigating through the items using the arrow keys:*/
.autocomplete-active-responsive {
    background-color: DodgerBlue !important;
    color: #ffffff;
}
