@media only screen and (max-width: 420px) {
     .search-responsive-size {
       height: 30px !important;
       padding: .375rem .75rem;
         /*padding: 0px;*/
     }
     .responsive-search-select select {
         padding: 0px;
         width: 120px;
     }
    .autocomplete-responsive {
        width:150px;
    }
    .autocomplete-responsive input {
        border: 1px solid transparent;
        background-color: #f1f1f1;
        padding: 5.5px;
        font-size: 12px;
    }
}