 /*  User drop down menu  */
.dropdown-user {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    margin-top: 0.7rem;
    right: 0;
    display: none;
    background-color: white;
    z-index: 10000;width: 120px;
}
.breadcrumb {
    padding: 0;
    margin-top: 1rem;
}
.breadcrumb li {
    position: relative;
}
.breadcrumb li:hover > ul{
    visibility: visible;
    opacity: 1;
    display: block;
}
.breadcrumb li ul:hover {
    visibility: visible;
    opacity: 1;
    display: block;
}
.dropdown-user li {
    clear: both;
    width: 100%;
    padding: 2%;
}

ul li:hover > ul,
ul li ul:hover {
    visibility: visible;
    opacity: 1;
    display: block;
}

.user-menu {
    padding-left: 0.7rem;padding-right: 0.7rem;
    padding-bottom: 0rem;
}
.User-area {
    width: 30px;
    height: 30px;
    position: relative;
    cursor: pointer;direction: rtl;
}
.User-area > .User-avtar {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    border: 2px solid #fff;
    box-shadow: 0px 0px 12px -5px #000;
}
.User-area > .User-avtar > img {
    width: 100%;
    height: 100%;
    border-radius: 30px;
}

.User-Dropdown {
    display: none;
    position: absolute;
    border-radius: 7px;
    background: #fff;
    box-shadow: 0px 0px 8px rgba(214, 214, 214, 0.78);
    list-style: none;
    padding: 0 20px;
    width: 200px;
    margin: 0;
    top: 70px;
    right: -9%;
    z-index: 10000;
}
.User-Dropdown:before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    margin-left: -0.5em;
    right: 0px;
    box-sizing: border-box;
    border: 7px solid black;
    border-color: transparent transparent #ffffff #ffffff;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    box-shadow: -3px 3px 3px -3px rgba(214, 214, 214, 0.78);
}
.User-Dropdown.U-open {
    display: block;
}
.User-Dropdown > li {
    padding: 0px;
    line-height: 47px;
    border-bottom: 1px solid rgba(215, 215, 215, 0.17);
}
.User-Dropdown > li:last-child {
    border-bottom: 0px;
}
.User-Dropdown > li > a {
    font-size: 13px;
    padding: 0px 25px 0px 10px;
    text-decoration: none;
    color: #1787e0;
    -webkit-transition: all .2s ease-out;
    transition: all .2s ease-out;
    display: block;
}
.User-Dropdown span {
    background: #16d67a;
    padding: 3px 10px;
    color: #fff;
    border-radius: 30px;
    font-size: 13px;
}
.User-Dropdown > li:before {
    content: '';
    width: 0px;
    height: 40px;
    position: absolute;
    background: #2196F3;
    margin-top: 4px;
    border-radius: 0 1px 1px 0;
    right: 0px;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}
.User-Dropdown > li:hover:before {
    width: 5px;
    border-radius: 30px;
}
.User-Dropdown > li a:hover {
    margin-right: 5px;
}


